import React from 'react'
import Tour from './Tour'
const Tours = ({ tours, quitarPais }) => {
  return (
    <section>
      <div className="title">
        <h2>Destinos Turistico de Sudamerica</h2>
        <div className="underline"></div>
      </div>
      <div>
        {tours.map((paises) => {
          const { id } = paises
          return <Tour key={id} {...paises} quitarPais={quitarPais} />
        })}
      </div>
    </section>
  )
}

export default Tours
