import React, { useState } from 'react'

const Tour = ({ id, image, info, name, price, quitarPais }) => {
  const [leer, leerMas] = useState(false)
  const [textBtn, cambiarTextoBtn] = useState('Leer más')
  function leerToggle() {
    if (info.length < 200) {
      cambiarTextoBtn('')
    }
    if (leer === false) {
      leerMas(true)
      cambiarTextoBtn('Leer menos')
    } else {
      leerMas(false)
      cambiarTextoBtn('Leer más')
    }
  }
  return (
    <article className="single-tour">
      <img src={image} alt={name} />
      <footer>
        <div className="tour-info">
          <h4>{name}</h4>
          <h4 className="tour-price">${price}</h4>
        </div>
        <p>
          {leer ? info : info.substring(0, 200)}...
          <button onClick={() => leerToggle()}>{textBtn}</button>
        </p>
        <button
          type="button"
          className="delete-btn"
          onClick={() => quitarPais(id)}
        >
          No me Interesa
        </button>
      </footer>
    </article>
  )
}

export default Tour
