import React, { useState, useEffect } from 'react'
import Loading from './Loading'
import Tours from './Tours'
import Lugares from './lugares'
// ATTENTION!!!!!!!!!!
// I SWITCHED TO PERMANENT DOMAIN
function App() {
  const [cargando, enCarga] = useState(true)
  const [tours, setTours] = useState([])
  //Fetch Los sitios turisticos
  const quitarPais = (id) => {
    const nuevosTour = tours.filter((tour) => tour.id !== id)
    setTours(nuevosTour)
  }
  const fetchTours = () => {
    enCarga(true)
    try {
      enCarga(false)
      setTours(Lugares)
    } catch (error) {
      enCarga(false)
      console.log(error)
    }
  }
  useEffect(() => {
    fetchTours()
  }, [])
  if (cargando) {
    return (
      <main>
        <Loading />
      </main>
    )
  }
  if (tours.length === 0) {
    return (
      <main>
        <div className="title">
          <h2>Ningún Lugar Elegido</h2>
          <button onClick={fetchTours} className="btn">
            Cargar Destinos
          </button>
        </div>
      </main>
    )
  }
  return (
    <main>
      <Tours tours={tours} quitarPais={quitarPais} />
    </main>
  )
}

export default App
